import React from 'react';
import { Form, Row, Col, Button } from 'react-bootstrap';
import PopUp from '../../../components/popUp';
import './index.less';
import { config } from '../../../globalConfig';
import { isMainThread } from 'worker_threads';

interface state {
    validated: boolean;
    idNum: boolean;
    popUp: boolean;
    content: string;
}

export default class page extends React.Component<{}, state>{
    idNum: number | undefined = undefined;
    title: string = '';
    introduce: string = '';
    formData: any;

    constructor(props: {}) {
        super(props);
        this.state = {
            validated: false,
            idNum: false,
            popUp: false,
            content: ''
        }
    }

    check = (dom: any, reg: any) => {
        if (dom) {
            if (!reg.test((dom.value.trim()))) {
                return false;
            } else {
                return true;
            }
        }
        return false;
    }

    checkValidity = () => {
        let idNumberBool: boolean;

        let idNumber: any = document.getElementById('idNumber');
        let reg = /^((\d{18})|(\d{17}x))$/;
        idNumberBool = this.check(idNumber, reg);

        if (idNumberBool) {
            this.idNum = idNumber.value.trim();
        } else {
            this.setState(() => ({
                popUp: true,
                content: ',!'
            }));
            return false;
        }
        return true;
    }

    handleSubmit = (event: any) => {
        let returnVal = this.checkValidity();
        event.preventDefault();
        event.stopPropagation();
        if (returnVal) {
            this.formData.append('title', this.title);
            this.formData.append('introduce', this.introduce);
            this.formData.append('idNum', this.idNum);

            fetch(config.domain + "hosUploadResult", {
                method: 'POST',
                credentials: 'include',
                body: this.formData
            }).then(res => {
                if (res.status >= 200 && res.status < 300) {
                    return res.json();
                } else {
                    throw new Error(res.statusText);
                }
            }).then(val => {
                this.setState(() => ({
                    popUp: true,
                    content: val.message
                }))
                if (val.message === '!') {
                    let form: any = document.getElementById("hos-upload-form");
                    form.reset();
                    let selectImg: any = document.getElementById('hosUpdateImgSelect');
                    selectImg.value = '';

                    let img: any = document.getElementById('hosUpdateImg');
                    img.value = '';
                    img.setAttribute('src', '');
                }
            })
        }
    }

    titleChange = (event: any) => {
        let val = event.target.value.trim();
        this.title = val;
    }

    introduceChange = (event: any) => {
        let val = event.target.value.trim();
        this.introduce = val;
    }

    componentDidMount() {
        let _this = this;
        let img: any = document.getElementById('hosUpdateImg');
        let selectImg: any = document.getElementById('hosUpdateImgSelect');
        selectImg.addEventListener('change', function (event: any) {
            var filePath = event.target.value,         //inputvalue
                fileFormat = filePath.substring(filePath.lastIndexOf(".")).toLowerCase(),
                src = window.URL.createObjectURL(event.target.files[0]); //
            // 
            if (!fileFormat.match(/.png|.jpg|.jpeg/)) {
                _this.setState(() => ({
                    popUp: true
                }))
                event.target.value = '';
                return;
            }
            img.setAttribute('src', src);
            _this.formData = new FormData();
            _this.formData.append('file', selectImg.files[0]);
        });
    }

    render() {
        return (
            <>
                {this.state.popUp ? (<PopUp content={this.state.content} />) : ''}
                <div className="hos-upload-container">
                    <Form id="hos-upload-form" className="form" validated={this.state.validated} onSubmit={this.handleSubmit}>
                        <Form.Group as={Row} controlId="formHorizontalID">
                            <Form.Label column sm={{ span: 2, offset: 2 }}>
                                
                            </Form.Label>
                            <Col sm={7}>
                                <Form.Control id="idNumber" required type="input" placeholder="1817x" />
                            </Col>
                        </Form.Group>
                        <br />

                        <Form.Group as={Row} controlId="formHorizontalNickName">
                            <Form.Label column sm={{ span: 3, offset: 1 }}>
                                <br />(pngjpgjpeg)
                        </Form.Label>
                            <Col sm={7}>
                                <Form.Control id="hosUpdateImg" as="img" />
                                <Form.Control id="hosUpdateImgSelect" required type="file" />
                            </Col>
                        </Form.Group>
                        <br />

                        <Form.Group as={Row} controlId="formHorizontalNickName">
                            <Form.Label column sm={{ span: 3, offset: 1 }}>
                                
                            </Form.Label>
                            <Col sm={7}>
                                <Form.Control id="contact" required type="input" placeholder="" onChange={this.titleChange} />
                            </Col>
                        </Form.Group>
                        <br />


                        <Form.Group as={Row} controlId="formHorizontalNickName">
                            <Form.Label column sm={{ span: 3, offset: 1 }}>
                                
                        </Form.Label>
                            <Col sm={7}>
                                <Form.Control id="introduce" required as="textarea" rows={5} placeholder="" onChange={this.introduceChange} />
                            </Col>
                        </Form.Group>
                        <br />

                        <Form.Group as={Row}>
                            <Col sm={{ offset: 4, span: 6 }}>
                                <Button type="submit" block></Button>
                            </Col>
                        </Form.Group>
                    </Form>
                </div>
            </>
        )
    }
}