import React from 'react';
import Title from '../../components/header/index';
import './index.less';
import { Link } from 'react-router-dom';
import { Table } from 'react-bootstrap';

interface props {
    location: any;
}

export default class page extends React.Component<props, {}> {
    reservationListDetail: any[] = [];
    componentWillMount() {
        console.log(this.props.location.state);
        this.reservationListDetail = this.props.location.state.reservationListDetail;
    }

    render() {
        return (
            <div className="hos-detail-container">
                <div className="header">
                    <Title content="" />
                    <Link to="/hospital/features" className="return-btn"></Link>
                </div>
                <div className="body">
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.reservationListDetail.map((el, index) => {
                                return (
                                    <tr>
                                        <td>{index + 1}</td>
                                        <td>{el.idNum}</td>
                                        <td>{el.workNum}</td>
                                        <td>{el.name}</td>
                                        <td>{el.time}</td>
                                    </tr>
                                )
                            })}
                        </tbody>
                    </Table>
                </div>
            </div>
        )
    }
}