import React from 'react';
import { Carousel } from 'react-bootstrap';
import sleep from '../../img/healthyTips/sleep.png';
import breakfast from '../../img/healthyTips/breakfast.png';
import jujube from '../../img/healthyTips/jujube.png';
import { Link } from 'react-router-dom';
import './tips.less';

export default class tips extends React.Component {

    toTips = (page: number) => {
        let tip: any;
        if (page === 1)
            tip = document.getElementById("tipsLink1");
        else if (page === 2)
            tip = document.getElementById("tipsLink2");
        else if (page === 3)
            tip = document.getElementById("tipsLink3");
        tip?.click();
    }

    render() {
        return (
            <div className="tips-container">
                <h2 className="tips-title"></h2>
                <hr className="split-line" />
                <div className="tips-body">
                    <Link to={{
                        pathname: "/healthyTips",
                        state: { page: 1, content: '' }
                    }}
                        id="tipsLink1"
                    />
                    <Link to={{
                        pathname: "/healthyTips",
                        state: { page: 2, content: ' '}
                    }}
                        id="tipsLink2"
                    />
                    <Link to={{
                        pathname: "/healthyTips",
                        state: { page: 3, content: '' }
                    }}
                        id="tipsLink3"
                    />

                    <Carousel>
                        <Carousel.Item onClick={this.toTips.bind(this, 1)}>
                            <img
                                className="d-block w-100"
                                src={sleep}
                                alt=""
                            />
                            <Carousel.Caption>
                                <h3></h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item onClick={this.toTips.bind(this, 2)}>
                            <img
                                className="d-block w-100"
                                src={breakfast}
                                alt=" "
                            />

                            <Carousel.Caption>
                                <h3> </h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item onClick={this.toTips.bind(this, 3)}>
                            <img
                                className="d-block w-100"
                                src={jujube}
                                alt=""
                            />

                            <Carousel.Caption>
                                <h3></h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                    </Carousel>
                </div>
            </div>
        )
    }
}